<template>
  <div>
      <div class="container_form">
         <div  v-if="numForm==1 || numForm==2">
            <h2>Расчет укладки ламината</h2>
            <div v-if="numForm==1" id="first-form">
                <div class="d-flex flex-column align-items-start input">
                    <label for="metr">МЕТРАЖ КОМНАТЫ</label>
                    <input v-model="data.metr" :class="{invalid:errors.metr}" type="number" name="metr" placeholder="Введите метраж помещения в кв.м">
                </div>
                <div class="d-flex flex-column align-items-start input">
                    <label for="count">СКОЛЬКО КВ.М В УПАКОВКЕ</label>
                    <input v-model="data.count_kv" :class="{invalid:errors.count_kv}" type="number" name="count_kv" placeholder="1.1">
                </div>
                <div class="check">
                    <h3>СПОСОБ УКЛАДКИ ЛАМИНАТА</h3>
                <div class="checkbox row">
                        <div class="col-6">
                            <input v-model="data.sposob" id="check1" type="checkbox" value="1">
                            <label for="check1">Прямой</label>
                        </div>
                        <div class="col-6">
                            <input v-model="data.sposob" id="check2" type="checkbox" value="2">
                            <label for="check2">От угла</label>
                        </div>
                    </div>
                </div>
                <hr>
                <div class="num">
                    <p>Колличество упаковок</p>
                    <span>12</span>
                </div>
                <div class="button-block">
                    <button @click="CheckForm()">ДАЛЕЕ</button>
                </div>
            </div>
            <div v-if="numForm==2" id="second-form">
                <div class="d-flex flex-column align-items-start input">
                    <label for="metr">ВАШЕ ИМЯ</label>
                    <input v-model="data.name" :class="{invalid:errors.name}" type="text" name="name">
                </div>
                <div class="d-flex flex-column align-items-start input">
                    <label for="count">EMAIL</label>
                    <input v-model="data.email" :class="{invalid:errors.email}" type="text" name="email">
                </div>
                <div class="button-block">
                    <button @click="SendForm()">ОТПРАВИТЬ</button>
                </div>
            </div>
        </div> 
         <div v-if="numForm==3"><h2>{{result}}</h2></div>
      </div>
  </div>
</template>

<script>
export default {

    data () {
        return {
            numForm:1,
            result:null,
            errors: {
                metr:false,
                count_kv:false,
                name:false,
                email:false,
            },
            

            data: {
                metr:null,
                count_kv:null,
                sposob:[],
                name:null,
                email:null,
            }
        }
    },
    methods: {

        CheckForm() {
            if (this.numForm==1) {
                if (this.data.metr && this.data.count_kv) this.numForm=2;
                else {
                    if(!this.data.metr) this.errors.metr=true;
                    if(!this.data.count_kv) this.errors.count_kv=true;
                }
            }
            else if (this.numForm==2) {
                if (this.data.name && this.data.email) {
                    return true;
                }
                else {
                    if(!this.data.name) this.errors.name=true;
                    if(!this.data.email) this.errors.email=true;
                }
            }
            return false;
        },

        SendForm() {
            if (this.CheckForm()) {
                var data = this.data;
                var url = "backend.php"
                    this.$http.get(url,{params : {data}}).then(response => {
                    this.result=response.data;
                    this.numForm=3;
                    }, response => {
                });
            }
        }
    },
}
</script>

<style scoped>
    .container_form {
        width: 600px;
        margin: 0 auto;
        padding: 10px;
    }

    .container_form h2 {
        text-align: center;
        font-size: 20px;
        margin-bottom: 25px;
    }

    .input {
        margin-bottom: 25px;
    }
    .input input {
        width: 100%;
        padding: 8px 10px;
        font-size: 10px;
        background: #efefef;
        border: 1px solid #efefef
    }
    .input label {
        font-size: 14px;
        font-weight: bold;
    }

    .input .invalid {
        border: 1px solid red;
    }

.check {
    margin-bottom: 25px;
}

.check h3 {
    font-size: 14px;
    font-weight: bold;
}

.check label {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding-left: 25px;
	margin-right: 15px;
	font-size: 13px;
}

input[type=checkbox] {
	display: none;
}
label:before {
	content: "";
	display: inline-block;

	width: 16px;
	height: 16px;

	margin-right: 10px;
	position: absolute;
	left: 0;
	bottom: 1px;
	background-color: #efefef;
}

.checkbox label {
	margin-bottom: 10px;
}

input[type=checkbox]:checked + label:before {
	content: "\2713";
	text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
	font-size: 8px;
	color: #fff;
    background: #000;
	text-align: center;
    line-height: 15px;
}

.num {
    text-align: right;
}

.num p {
    margin-bottom: 5px;
    font-size: 10px;
}

.num span {
    font-weight: bold;
    font-size: 14px;
}
.button-block {
    text-align: center;
}

.button-block button {
    padding: 6px 25px;
    font-size: 32px;
    color: #fff;
    background: #2d3a67;
    border: 0;
    width: 248px;
}
</style>


